import { Link } from "react-router-dom";
import BrandLogo from "../components/brand-logo";
import { Row, Col, Button } from "react-bootstrap";
import { Linkedin, ChevronUp } from "react-bootstrap-icons";

const Footer = () => {
  return (
    <footer className="p-5">
      <div className="d-lg-flex">
        <BrandLogo />
        <Row className="footer-links">
          <Col lg={2}>
            <p>
              <Link to="/about-ttcpp">About TTCPP</Link>
            </p>
            <p>
              <Link to="/membership">MemberShip</Link>
            </p>
            <p>
              <Link to="/life-retirement">Life in Retirement</Link>
            </p>
          </Col>
          <Col lg={2}>
            <p>
              <Link to="/investments">Investments</Link>
            </p>
            <p>
              <Link to="/resources">Resources</Link>
            </p>
            <p>
              <Link to="/contact-us">Contact Us</Link>
            </p>
          </Col>
          <Col lg={2}>
            <p>
              <Link to="/privacy">Privacy</Link>
            </p>
            <p>
              <Link to="/terms-of-service">Terms of Use</Link>
            </p>
            <p>
              <Link to="/accessibility">Accessibility</Link>
            </p>
          </Col>
          <Col lg={6} className="align-self-end text-end">
            <p className="fw-bold">
              CONNECT WITH US!
              <Linkedin size={24} className="ms-2" />
            </p>
            <p>
              2021 Toronto Transit Commission Pension Fund Society. All rights reserved.
            </p>
          </Col>
        </Row>
      </div>
      <Button
        variant="secondary"
        className="btn-move-top"
        onClick={() => {
          window.scrollTo({top: 0, left: 0, behavior: 'smooth'});
        }}
      >
        <ChevronUp size={24} />
      </Button>
    </footer>
  );
};
export default Footer;