import { Form, Button } from "react-bootstrap";
import DatePicker from "react-date-picker";

export const GenerateEstimate = (props) => {
  const {
    generateEstimate,
    handleChange,
    generate
  } = props;

  return (
    <>
      <Form.Group className="mb-3">
        <Form.Label>Earliest reduced retirement date</Form.Label>
        <DatePicker
          className="dob-picker"
          clearIcon={null}
          calendarClassName="dob-picker-calander"
          calendarIcon={null}
          dayPlaceholder="dd"
          monthPlaceholder="mm"
          yearPlaceholder="yyyy"
          minDate={new Date()}
          onChange={(v) => handleChange('estimatedRetirementDate', v)}
          value={generateEstimate.estimatedRetirementDate}
        />
      </Form.Group>
      <Button
        variant="danger"
        onClick={generate}
      >
        Generate Estimate
      </Button>
    </>
  );
};
  