import { Row, Col } from "react-bootstrap";
export const statusList = ['inactive', 'active', 'progress', 'complete'];
export const OneStepComponent = (props) => {
  const {
    index,
    title,
    description,
    status,
    current,
    steps,
    children,
  } = props;

  console.log(current);
  const oneHeight = steps ? 100/steps : 100;
  const bars = [];
  for (let i = 0; i < steps ; i++) {
    bars.push(
      <div
        key={i}
        className={`left-line ${current > i + 1 ? 'active' : ''}`}
        style={
          {
            height: `${oneHeight}%`,
            top: `${oneHeight * i}%`
          }
        }
      />
    );
  };

  return (
    <div className={`one-step ${statusList[status]}`}>
      <div className="d-flex">
        <div className="step-number-container me-3">
          <div className="step-number">{index}</div>
        </div>
        <Row className="flex-fill">
          <Col lg={4}>
            <div
              className="step-title"
              dangerouslySetInnerHTML={{__html: title}}
            />
            <div className="step-description">
              {description}
            </div>
          </Col>
          <Col lg={{span:4, offset: 1}}>
            {children}
          </Col>
        </Row>
      </div>
      {bars}
    </div>
  );
};